import { AbstractConnector } from '@web3-react/abstract-connector'
import { UnsupportedChainIdError, useWeb3React } from '@web3-react/core'
import { WalletConnectConnector } from '@web3-react/walletconnect-connector'
import { Row, Col, Form } from 'react-bootstrap';
import React, {useEffect, useMemo, useState} from 'react'
import { isMobile } from 'react-device-detect'
import ReactGA from 'react-ga'
import styled from 'styled-components'
import { injected } from '../../connectors'
import {NetworkContextName} from '../../constants'
import usePrevious from '../../hooks/usePrevious'
import { ApplicationModal } from '../../state/application/actions'
import { useModalOpen, useWalletModalToggle } from '../../state/application/hooks'
import AccountDetails from '../AccountDetails'
import { SUPPORTED_WALLETS } from '../../connectors';
import LedgerPaths from '../../constants/ledgerPaths';

import Modal from '../Modal'
import Option from './Option'
import PendingView from './PendingView'
import useENSName from "../../hooks/useENSName";
import {isTransactionRecent, useAllTransactions} from "../../state/transactions/hooks";
import {TransactionDetails} from "../../state/transactions/reducer";

const { Check } = Form;

const Wrapper = styled.div`
  ${({ theme }) => theme.flexColumnNoWrap};
  margin: 0;
  padding: 0;
  width: 100%;
`

const HeaderRow = styled.div`
  ${({ theme }) => theme.flexRowNoWrap};
  align-items: center;
  padding: 1rem 1rem;
  font-weight: 500;
  color: ${props => (props.color === 'blue' ? ({ theme }) => theme.primary1 : 'inherit')};
  ${({ theme }) => theme.mediaWidth.upToMedium`
    padding: 1rem;
  `};
`

const ContentWrapper = styled.div`
  background-color: ${({ theme }) => theme.bg2};
  padding: 1rem;
  border-bottom-left-radius: 0.42rem;
  border-bottom-right-radius: 0.42rem;

  ${({ theme }) => theme.mediaWidth.upToMedium`padding: 1rem`};
`

const UpperSection = styled.div`
  position: relative;

  h5 {
    margin: 0;
    margin-bottom: 0.5rem;
    font-size: 1rem;
    font-weight: 400;
  }

  h5:last-child {
    margin-bottom: 0px;
  }

  h4 {
    margin-top: 0;
    font-weight: 500;
  }
`

const HoverText = styled.div`
  :hover {
    cursor: pointer;
  }
`

const OptionGrid = styled.div`
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 1rem;
`


const WALLET_VIEWS = {
  OPTIONS: 'options',
  OPTIONS_SECONDARY: 'options_secondary',
  ACCOUNT: 'account',
  PENDING: 'pending',
  LEDGER_PATH: 'ledger_select_path',
}

// we want the latest one to come first, so return negative if a is after b
function newTransactionsFirst(a: TransactionDetails, b: TransactionDetails) {
  return b.addedTime - a.addedTime
}

export default function WalletModal() {
  // important that these are destructed from the account-specific web3-react context
  const { active, account, connector, activate, error } = useWeb3React()
  const [selectedPath, setSelectedPath] = useState<string>(LedgerPaths[0].path)
  const [customPath, setCustomPath] = useState<string>('')

  const contextNetwork = useWeb3React(NetworkContextName)

  const { ENSName } = useENSName(account ?? undefined)

  const allTransactions = useAllTransactions()

  const sortedRecentTransactions = useMemo(() => {
    const txs = Object.values(allTransactions)
    return txs.filter(isTransactionRecent).sort(newTransactionsFirst)
  }, [allTransactions])

  const pendingTransactions = sortedRecentTransactions.filter(tx => !tx.receipt).map(tx => tx.hash)
  const confirmedTransactions = sortedRecentTransactions.filter(tx => tx.receipt).map(tx => tx.hash)


  const [walletView, setWalletView] = useState(WALLET_VIEWS.ACCOUNT)

  const [pendingWallet, setPendingWallet] = useState<AbstractConnector | undefined>()
  const [isLedger, setIsLedger] = useState<boolean>(false);

  const [pendingError, setPendingError] = useState<boolean>()

  const walletModalOpen = useModalOpen(ApplicationModal.WALLET)
  const toggleWalletModal = useWalletModalToggle()

  const previousAccount = usePrevious(account)

  // close on connection, when logged out before
  useEffect(() => {
    if (account && !previousAccount && walletModalOpen) {
        toggleWalletModal()
    }
  }, [account, previousAccount, toggleWalletModal, walletModalOpen])

  // always reset to account view
  useEffect(() => {
    if (walletModalOpen) {
      setPendingError(false)
      setWalletView(WALLET_VIEWS.ACCOUNT)
    }
  }, [walletModalOpen])

  // close modal when a connection is successful
  const activePrevious = usePrevious(active)
  const connectorPrevious = usePrevious(connector)
  useEffect(() => {
    if (walletModalOpen && ((active && !activePrevious) || (connector && connector !== connectorPrevious && !error))) {
      setWalletView(WALLET_VIEWS.ACCOUNT)
    }
  }, [setWalletView, active, error, connector, walletModalOpen, activePrevious, connectorPrevious])

  if (!contextNetwork.active && !active) {
    return null
  }
  const handleConnectLedger = () => {
    let path = selectedPath;
    if(path === 'custom') {
      path = customPath;
    }

    const ledger = SUPPORTED_WALLETS.ledger;
    tryActivation(ledger.connector(path), true)
  }
  const tryActivation = async (connector: AbstractConnector | undefined, ledgerConnect = false) => {
    setIsLedger(ledgerConnect)
    let name = ''
    Object.keys(SUPPORTED_WALLETS).map(key => {
      if (connector === SUPPORTED_WALLETS[key].connector) {
        return (name = SUPPORTED_WALLETS[key].name)
      }
      return true
    })
    // log selected wallet
    ReactGA.event({
      category: 'Wallet',
      action: 'Change Wallet',
      label: name
    })
    setPendingWallet(connector) // set wallet for pending view
    setWalletView(WALLET_VIEWS.PENDING)

    // if the connector is walletconnect and the user has already tried to connect, manually reset the connector
    if (connector instanceof WalletConnectConnector && connector.walletConnectProvider?.wc?.uri) {
      connector.walletConnectProvider = undefined
    }

    connector &&
      activate(connector, undefined, true).catch(error => {
        if (error instanceof UnsupportedChainIdError) {
          activate(connector) // a little janky...can't use setError because the connector isn't set
        } else {
          setPendingError(true)
        }
      })
  }

  // get wallets user can switch too, depending on device/browser
  function getOptions() {
    const isMetamask = window.ethereum && window.ethereum.isMetaMask
    return Object.keys(SUPPORTED_WALLETS).map(key => {
      const option = SUPPORTED_WALLETS[key]
      // check for mobile options
      if (isMobile) {


        if (!window.web3 && !window.ethereum && option.mobile) {
          return (
            <Option
              onClick={() => {
                option.connector !== connector && !option.href && tryActivation(option.connector)
              }}
              id={`connect-${key}`}
              key={key}
              active={option.connector && option.connector === connector}
              color={option.color}
              link={option.href}
              header={option.name}
              subheader={null}
              type={key}
            />
          )
        }
        return null
      }

      // overwrite injected when needed
      if (option.connector === injected) {
        // don't show injected if there's no injected provider
        if (!(window.web3 || window.ethereum)) {
          if (option.name === 'MetaMask') {
            return (
              <Option
                id={`connect-${key}`}
                key={key}
                color={'#E8831D'}
                header={'Install Metamask'}
                subheader={null}
                link={'https://metamask.io/'}
                type={'metamask'}
              />
            )
          } else {
            return null //dont want to return install twice
          }
        }
        // don't return metamask if injected provider isn't metamask
        else if (option.name === 'MetaMask' && !isMetamask) {
          return null
        }
        // likewise for generic
        else if (option.name === 'Injected' && isMetamask) {
          return null
        }
      }

      // return rest of options
      return (
        !isMobile &&
        !option.mobileOnly && (
          <Option
            id={`connect-${key}`}
            onClick={() => {
              option.connector === connector
                ? setWalletView(WALLET_VIEWS.ACCOUNT)
                : key === 'ledger' ? setWalletView(WALLET_VIEWS.LEDGER_PATH) : !option.href && tryActivation(option.connector)
            }}
            key={key}
            active={option.connector === connector}
            color={option.color}
            link={option.href}
            header={option.name}
            subheader={null} //use option.descriptio to bring back multi-line
            type={key}
          />
        )
      )
    })
  }

  function getModalContent() {
    if (error) {
      return (
        <UpperSection>
          <HeaderRow>{error instanceof UnsupportedChainIdError ? 'Wrong Network' : 'Error connecting'}</HeaderRow>
          <ContentWrapper>
            {error instanceof UnsupportedChainIdError ? (
              <h5>Please connect to the appropriate Ethereum network.</h5>
            ) : (
              'Error connecting. Try refreshing the page.'
            )}
          </ContentWrapper>
        </UpperSection>
      )
    }
    if (account && walletView === WALLET_VIEWS.ACCOUNT) {
      return (
        <AccountDetails
          toggleWalletModal={toggleWalletModal}
          pendingTransactions={pendingTransactions}
          confirmedTransactions={confirmedTransactions}
          ENSName={ENSName}
          openOptions={() => setWalletView(WALLET_VIEWS.OPTIONS)}
        />
      )
    }
    if(walletView === WALLET_VIEWS.LEDGER_PATH) {
      return (
          <UpperSection>
            <HeaderRow color="blue">
              <button className={'btn btn-light-primary mr-4'}
                      onClick={() => {
                        setPendingError(false)
                        setWalletView(WALLET_VIEWS.ACCOUNT)
                      }}
              >
                Back
              </button>
              <HoverText>Select HD Derivation path</HoverText>
            </HeaderRow>
            <ContentWrapper>
              <Row className={'row-paddingless'}>
                {LedgerPaths.map((item, index) => {
                  return (
                      <Col xs={12} lg={4} md={6} key={`hd-path-${index}`}>
                        <Check type={'radio'} id={`hd-path-${index}`} className={'d-flex align-items-center py-3'}>
                          <Check.Input type={'radio'} name={'hd-path'} checked={item.path === selectedPath} onChange={() => setSelectedPath(item.path)} />
                          <div className={'d-flex flex-column pl-3'}>
                            <Check.Label>{item.path}</Check.Label>
                            <Check.Label className={'text-muted font-size-sm'}>{item.label}</Check.Label>
                          </div>
                        </Check>
                      </Col>
                  )
                })}

                <Col xs={12} lg={4} md={6} key={`hd-path-custom`}>
                  <Check type={'radio'} id={`hd-path-custom`} className={'d-flex align-items-center mb-3'}>
                    <Check.Input type={'radio'} name={'hd-path'} checked={selectedPath === 'custom'} onChange={() => setSelectedPath('custom')}/>
                    <div className={'d-flex flex-column pl-3 pt-1'}>
                      <Check.Label>Or Add Custom path</Check.Label>
                    </div>
                  </Check>
                </Col>
                {selectedPath === 'custom' && (
                    <Col xs={12}>
                      <Form.Control
                        placeholder={"m/44'/60'/0'/0"}
                        onChange={e => setCustomPath(e.target.value)}
                        value={customPath}
                        className={'mb-3'}
                      />
                    </Col>
                )}

                <Col xs={12} className={'d-flex flex-row-reverse mt-4'}>
                  <button className={'btn btn-primary ml-2'}
                          onClick={handleConnectLedger}
                  >
                    Connect to Wallet
                  </button>
                  <button className={'btn btn-outline-primary'}
                          onClick={() => {
                            setPendingError(false)
                            setWalletView(WALLET_VIEWS.ACCOUNT)
                          }}
                  >
                    Cancel
                  </button>
                </Col>

              </Row>
            </ContentWrapper>
          </UpperSection>
      )
    }
    return (
      <UpperSection>
        {walletView !== WALLET_VIEWS.ACCOUNT ? (
          <HeaderRow color="blue">
            <button className={'btn btn-light-primary'}
              onClick={() => {
                setPendingError(false)
                setWalletView(WALLET_VIEWS.ACCOUNT)
              }}
            >
              Back
            </button>
          </HeaderRow>
        ) : (
          <HeaderRow>
            <HoverText>Connect to a wallet</HoverText>
          </HeaderRow>
        )}
        <ContentWrapper>
          {walletView === WALLET_VIEWS.PENDING ? (
            <PendingView
              connector={pendingWallet}
              error={pendingError}
              setPendingError={setPendingError}
              tryActivation={tryActivation}
              isLedger={isLedger}
            />
          ) : (
            <OptionGrid>{getOptions()}</OptionGrid>
          )}
        </ContentWrapper>
      </UpperSection>
    )
  }

  return (
    <Modal isOpen={walletModalOpen} onDismiss={toggleWalletModal} minHeight={false} maxHeight={90} maxWidth={walletView === WALLET_VIEWS.LEDGER_PATH ? 740 : 580}>
      <Wrapper>{getModalContent()}</Wrapper>
    </Modal>
  )
}
